.sr-only,
.screen-reader-text {
  position: absolute;
  width: 1px;
  height: 1px;
  padding: 0;
  margin: -1px;
  overflow: hidden;
  clip: rect(0, 0, 0, 0);
  border: 0;
}

.sr-only-focusable:active,
.screen-reader-text:active,
.sr-only-focusable:focus,
.screen-reader-text:focus {
  position: static;
  width: auto;
  height: auto;
  margin: 0;
  overflow: visible;
  clip: auto;
}

/**
 * Import npm dependencies
 *
 * Prefix your imports with `~` to grab from node_modules/
 * @see https://github.com/webpack-contrib/sass-loader#imports
 */

/** Import theme styles */

body {
  font-size: 19px;
  font-family: "News Cycle", -apple-system, system-ui, BlinkMacSystemFont, "Segoe UI", Roboto, "Helvetica Neue", Arial, sans-serif;
  letter-spacing: 0.02em;
  background-color: #fff;
}

a,
a:visited {
  color: #46166b;
  transition: all ease-in-out 350ms;
}

a:hover {
  color: #555;
}

.nav > li {
  font-size: 1rem;
}

.nav > li a,
.nav > li a:visited {
  padding: 0.5rem 0.85rem;
  display: inline-block;
}

.p--primary,
.card--faculty-and-staff .sub-field,
.component-wrap p.lead,
.component-wysiwyg-wrap p.lead,
.component-wrap p,
.component-wrap li,
.component-wysiwyg-wrap p,
.component-wysiwyg-wrap li {
  font-size: 1rem;
}

.h3--primary,
.card--faculty-and-staff h3,
.card--faculty-and-staff h4,
.card--faculty-and-staff h5,
.component-wrap h3,
.component-wysiwyg-wrap h3,
.main > article h3 {
  font-size: 1.5rem;
}

.h4--primary,
.component-event-block-wrap h3,
.component-news-block-wrap h3,
.component-wrap h4,
.component-wysiwyg-wrap h4 {
  font-size: 1.25rem;
}

.h5--primary,
.component-wrap h5,
.component-wysiwyg-wrap h5 {
  font-size: 1.1rem;
}

h1.single-title {
  font-family: "Trade Gothic LT Com Bold Cn", Arial, Sans-serif;
  text-transform: uppercase;
  font-size: 4rem;
  line-height: 1.2em;
}

h2.single-subtitle {
  font-family: "Trade Gothic LT Com Bold Cn", Arial, Sans-serif;
  font-size: 1.8rem;
  margin: 1rem 0 0.5rem;
  opacity: 0.85;
}

.entry-content h2 {
  margin: 0.75em 0;
}

.video-wrapper {
  position: relative;
  padding-bottom: 56.25%;
  /* 16:9 */
  padding-top: 25px;
  height: 0;
}

.video-wrapper iframe {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  border: none;
}

table {
  border: 1px solid #c5ccd2;
}

table tbody tr:nth-child(odd) {
  background-color: #fff;
}

table tbody tr td {
  padding: 5px;
}

.dataTables_wrapper {
  font-size: 0.9rem;
}

.dataTables_wrapper .row {
  width: 100%;
}

.dataTables_wrapper .form-control,
.dataTables_wrapper .comment-form input[type="text"],
.comment-form .dataTables_wrapper input[type="text"],
.dataTables_wrapper
  .comment-form input[type="email"],
.comment-form .dataTables_wrapper input[type="email"],
.dataTables_wrapper
  .comment-form input[type="url"],
.comment-form .dataTables_wrapper input[type="url"],
.dataTables_wrapper
  .comment-form textarea,
.comment-form .dataTables_wrapper textarea,
.dataTables_wrapper .search-form .search-field,
.search-form .dataTables_wrapper .search-field {
  margin: 0 0.5rem;
}

.dataTables_wrapper .dataTables_filter .form-control,
.dataTables_wrapper .dataTables_filter .comment-form input[type="text"],
.comment-form .dataTables_wrapper .dataTables_filter input[type="text"],
.dataTables_wrapper .dataTables_filter
  .comment-form input[type="email"],
.comment-form .dataTables_wrapper .dataTables_filter input[type="email"],
.dataTables_wrapper .dataTables_filter
  .comment-form input[type="url"],
.comment-form .dataTables_wrapper .dataTables_filter input[type="url"],
.dataTables_wrapper .dataTables_filter
  .comment-form textarea,
.comment-form .dataTables_wrapper .dataTables_filter textarea,
