.front-header {
  padding: 20vh 0 60px;
  color: $white;
  position: relative;
  background-color: $gray;
  overflow: hidden;

  .front-photo {
    position: absolute;
    z-index: 1;
    width: 100%;
    height: 100%;
    top: 0;
    left: 0;
    background-size: cover;
    background-position: 50% 50%;
  }

  .front-header-content {
    background-color: $darkgray; // Fallback
    background-color: $black-transparent-50;
    width: auto;
    max-width: 50%;
    display: inline-block;
    padding: $spacing-extra-large;
    position: relative;
    z-index: 5;
    text-shadow: 0.2rem 0.2rem 1rem rgba(0, 0, 0, 0.25);
    margin-bottom: $spacing-small;
  }

  h2.front-title {
    font-family: $font-headings;
    text-transform: uppercase;
    font-size: 4rem;
    line-height: 1.2em;
    margin-bottom: 1rem;
  }

  h3.front-subtitle {
    font-family: $font-headings;
    font-size: 1.8rem;
    margin: 1rem 0;
    opacity: 0.85;
    color: $white;
  }

  &__form {
    select {
      margin-right: 1rem;
      margin-bottom: 15px;
      max-width: 200px;
    }
  }
}

.discover-regions h3 {
  font-size: 1.35rem;
}

body.home {
  section {
    width: 100%;

    &.page-section--tall {
      min-height: 40vh;
    }
  }
}

#homepage-myths {
  background-color: $offwhite;
  background-image: url(../images/newbackgroundimg.jpg);
  background-repeat: repeat;
  color: $purple;
  min-height: auto;
}

#homepage-cta {
  .btn:first-of-type {
    color: $white;
    background-color: $purple;
    border-color: $purple;

    &:hover {
      color: $gray;
      background-color: $white;
      border-color: $gray;
    }
  }
}

#homepage-social {
  background-color: $purple;
  color: $white;

  h3 {
    font-size: 1rem;
    text-transform: uppercase;
  }

  .btn {
    color: $white;
    border-color: $white;
    margin-top: 60px;

    &:hover {
      color: $purple;
      border-color: $purple;
      background-color: $white;
    }
  }
}

#homepage-info {
  h2 {
    color: $midgray;

    strong {
      color: $purple;
    }
  }

  .btn {
    border-color: $purple;
    background-color: $purple;
    color: $white;

    &:hover {
      color: $purple;
      background-color: $white;
    }
  }
}

#homepage-discover {
  h2 {
    color: $midgray;
  }

  .row {
    margin: 0 auto;

    ul {
      list-style: none;
      padding-left: 0;
    }
  }
}



